.header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  padding: 35px 0;
  z-index: 2;
  &__container {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    @include flex-v-center;
    gap: 37px;
    &-btn {
      padding: 0px 40px;
      background: var(--btn-gradient);
      border-radius: 4px;
      font-weight: 700;
      font-size: 16px;
      line-height: 52px;
      letter-spacing: 0.5px;
      color: var(--light-color);
    }
    &-reg {
      font-weight: 700;
      font-size: 16px;
      line-height: 160%;
      color: var(--gray-color);
      &::before {
        content: url(../img/svg/profile.svg);
        position: relative;
        right: 9px;
        top: 4px;
      }
    }
  }
}
.logo {
  z-index: 50;
}
.menu {
  width: 100%;
  margin-left: 40px;
  @include flex-all-sb;
  transition: 0.5s;
}
.nav {
  &__list {
    display: flex;
    gap: 40px;
  }
  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--gray-color);
  }
}
.burger {
  @include burger;
  display: none;
}
@include tablet {
  .header__container {
    align-items: start;
    justify-content: space-between;
  }
  .burger {
    display: block;
    @include burger;
  }
  .menu {
    position: absolute;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    margin-left: 0px;
    transform: translateY(-100%);
    background: var(--light-color);
  }
  .header__right {
    margin-top: 40px;
  }
  .nav__list {
    flex-direction: column;
  }
  .menu--active {
    transform: translateY(0);
    transition: 0.5s;
  }
}
@include small-tablet {
  .header__right {
    gap: 20px;
  }
  .header__right-btn {
    padding: 0px 5px;
    font-size: 15px;
  }
  .header__right-reg {
    font-size: 12px;
  }
}
