:root {
  --font-family: "Lato", sans-serif;
  --content-width: 1245px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --primary-color: #ff3f3a;
  --btn-gradient: linear-gradient(55.95deg, #ff3f3a 0%, #f75e05 100%);
  --secondary-color: #ffcf2d;
  --divider-gradient: linear-gradient(
    270.01deg,
    rgba(218, 219, 221, 0) 0%,
    rgba(218, 219, 221, 0.5) 12.33%,
    #dadbdd 51.91%,
    rgba(218, 219, 221, 0.5) 87.85%,
    rgba(218, 219, 221, 0) 100%
  );
  --success-color: #03cea4;
  --danger-color: #ff4242;
  --warning-color: #f89828;
  --info-color: #5a87fc;
  --gray-color: #424551;
  --gray-color900: #1e212c;
  --gray-color700: #787a80;
  --pink-color:#f52f6e;
}

