.featured {
  padding: 10px 0 180px 0;
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 40px;
    margin-bottom: 60px;
  }
  &__head {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    margin-bottom: 8px;
  }
  &__title {
    margin: 0;
    font-weight: 900;
    font-size: 46px;
    line-height: 130%;
  }
  &__btn {
    cursor: pointer;
    padding: 0 40px;
    font-weight: 700;
    font-size: 16px;
    line-height: 52px;
    color: var(--danger-color);
    border-radius: 4px;
    border: 1px solid #ff4242;
    background: var(--light-color);
    transition: 0.5s;
    &:hover {
      color: var(--light-color);
      background: var(--btn-gradient);
      transition: 0.3s;
    }
  }
  &__column {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 30px;
  }
  &__card {
    display: flex;
    background: var(--light-color);
    border: 1px solid #e5e8ed;
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
      0px 30px 24px -10px rgba(154, 156, 165, 0.05),
      0px 12px 10px -6px rgba(154, 156, 165, 0.04),
      0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    border-radius: 4px;
    transition: 0.5s;
    &:hover {
      box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.16),
        0px 30px 24px -10px rgba(154, 156, 165, 0.1),
        0px 12px 10px -6px rgba(154, 156, 165, 0.08),
        0px 4px 4px -4px rgba(30, 33, 44, 0.05);
    }
    &-img {
      img {
        height: 100%;
        width: 100%;
        min-width: 214px;
      }
    }
    &-info {
      padding: 32px;
    }
    &-position {
      margin: 0;
      display: inline-block;
      padding: 1px 8px;
      border-radius: 4px;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: var(--light-color);
      margin-bottom: 20px;
    }
    &-desc {
      margin: 0;
      font-weight: 700;
      font-size: 20px;
      line-height: 150%;
      color: var(--gray-color900);
      margin-bottom: 20px;
    }
    &-price {
      font-weight: 700;
      font-size: 18px;
      line-height: 150%;
      color: var(--danger-color);
      &__span {
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        color: var(--gray-color700);
      }
    }
  }
}
.marketing {
  background: var(--success-color);
}
.management {
  background: var(--info-color);
}
.hr {
  background: var(--warning-color);
}
.design {
  background: var(--pink-color);
}
@include tablet {
  .featured{
    padding-bottom: 50px;
  }
  .featured__column {
    grid-template-columns: repeat(1, 1fr);
  }
}
@include mobile{
  .featured__card{
    flex-direction: column;
  }
  .featured__card-info{
    padding: 32px 10px;
  }
}
