.createx {
  padding: 180px 0;
  &__container {
    display: flex;
    gap: 40px;
    justify-content: space-between;
  }
  &__info {
    max-width: 358px;
  }
  &__head {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    color: var(--gray-color);
    margin-bottom: 8px;
  }
  &__title {
    margin: 0;
    font-weight: 900;
    font-size: 46px;
    line-height: 130%;
    color: var(--gray-color900);
    margin-bottom: 40px;
  }
  &__column {
    display: flex;
    flex-direction: column;
    gap: 12px;
    &-item {
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      color: var(--gray-color);
      &::before {
        content: url(../img/svg/check.svg);
        margin-right: 16px;
      }
    }
  }
  &__btn {
    margin-top: 48px;
    cursor: pointer;
    padding: 0 32px;
    font-weight: 700;
    font-size: 14px;
    line-height: 44px;
    color: var(--danger-color);
    border-radius: 4px;
    border: 1px solid #ff4242;
    background: var(--light-color);
    transition: 0.5s;
    &:hover {
      color: var(--light-color);
      background: var(--btn-gradient);
      transition: 0.3s;
    }
  }
}
@include small-tablet {
  .createx{
    padding: 100px 0;
  }
  .createx__container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .createx__column {
    gap: 30px;
  }
  .createx__info {
    max-width: none;
  }
}
