.hero {
  padding: 220px 0 97px 0;
  background: #ffdab9;
  position: relative;
  overflow: hidden;
  &__container {
    position: relative;
  }
  &__content {
    @include flex-all-sb;
    gap: 30px;
  }
  &__desc {
    max-width: 496px;
  }
  &__span {
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 14px;
    line-height: 150%;
    color: var(--gray-color900);
    margin-bottom: 24px;
    &::before {
      content: url(../img/svg/hero-span.svg);
      position: relative;
    }
  }
  &__title {
    margin: 0;
    font-weight: 900;
    font-size: 64px;
    line-height: 130%;
    color: var(--gray-color900);
    letter-spacing: 1px;
    margin-bottom: 60px;
  }
  &__btn {
    display: flex;
    gap: 25px;
    &-item {
      padding: 0 40px;
      font-weight: 700;
      font-size: 16px;
      line-height: 52px;
      color: var(--danger-color);
      border-radius: 4px;
      border: 1px solid #ff4242;
      transition: 0.5s;
      &:hover {
        color: var(--light-color);
        background: var(--btn-gradient);
        transition: 0.3s;
      }
    }
  }
  &__row {
    margin-top: 190px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    &-item {
      display: flex;
      align-items: center;
      gap: 12px;
    }
    &-num {
      font-weight: 900;
      font-size: 46px;
      line-height: 60px;
      color: var(--gray-color900);
    }
    &-text {
      font-weight: 400;
      font-size: 16px;
      line-height: 26px;
      color: var(--gray-color900);
    }
  }
}
@include tablet {
  .hero {
    padding: 100px 0 50px 0;
  }
  .hero__content {
    flex-direction: column;
  }
  .hero__row {
    margin-top: 50px;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
    justify-items: center;
  }
}
@include mobile {
  .hero__title {
    font-size: 40px;
  }
  .hero__btn-item {
    padding: 8px 22px;
    font-size: 15px;
  }
  .hero__row {
    grid-template-columns: repeat(1, 1fr);
  }
}
