.benefits {
  padding-bottom: 100px;
  &__header {
    text-align: center;
    margin-bottom: 70px;
  }
  &__head {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: var(--gray-color900);
    margin-bottom: 8px;
  }
  &__title {
    margin: 0;
    font-weight: 900;
    font-size: 46px;
    line-height: 130%;
    color: var(--gray-color900);
  }
  &__content {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }
  &__row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 90px;
    transition: 0.5s;
    &-item {
      display: inline-block;
      cursor: pointer;
      padding: 10px 70px;
      color: var(--gray-color700);
    }
    &-text {
      margin: 0;
      font-weight: 700;
      font-size: 16px;
      line-height: 160%;
      text-align: center;
    }
  }
  &__tab {
    display: none;
    align-items: center;
    justify-content: space-between;
  }
  &__info {
    max-width: 495px;
    &-title {
      margin: 0;
      font-weight: 900;
      font-size: 32px;
      line-height: 130%;
      color: var(--gray-color900);
      margin-bottom: 25px;
    }
    &-desc {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      color: var(--gray-color900);
    }
  }
}
.active-tab {
  border: 1px solid #ff3f3a;
  border-radius: 4px;
  color: var(--danger-color);
  transition: 0.5s;
}
.tab-active {
  display: flex;
}
@include tablet {
  .benefits__content {
    justify-content: space-between;
    flex-direction: row;
  }
  .benefits__row {
    flex-direction: column;
    gap: 50px;
  }
  .benefits__tab {
    flex-direction: column;
  }
  .benefits__row-item {
    padding: 10px 25px;
  }
  .benefits__row-text {
    font-size: 13px;
  }
}
@include mobile {
  .benefits__row-item{
    padding: 10px 10px;
  }
  .benefits__info-title {
    font-size: 24px;
  }
  .benefits__info-desc {
    font-size: 11px;
  }
}
