.events {
  padding: 80px 0 85px 0;
  background: #ffdab9;

  &__header {
    text-align: center;
    margin-bottom: 60px;
  }
  &__head {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: var(--gray-color900);
    margin-bottom: 8px;
  }
  &__title {
    margin: 0;
    font-weight: 900;
    font-size: 46px;
    line-height: 130%;
    color: var(--gray-color900);
  }
  &__column {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  &__card {
    width: 100%;
    background: var(--light-color);
    border-radius: 4px;
    display: flex;
    align-items: center;
    padding: 32px 40px;
    &-time {
      display: flex;
      align-items: center;
      gap: 20px;
      margin-right: 42px;
    }
    &-data {
      margin: 0;
      font-weight: 900;
      font-size: 48px;
      line-height: 130%;
      letter-spacing: 1px;
      color: var(--danger-color);
    }
    &-month {
      margin: 0;
      font-weight: 700;
      font-size: 20px;
      line-height: 150%;
      color: var(--gray-color900);
    }
    &-times {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      color: var(--gray-color700);
    }
    &-info {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
    }
    &-text {
      margin: 0;
      font-weight: 700;
      font-size: 20px;
      line-height: 150%;
      color: var(--gray-color900);
    }
    &-desc {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      color: var(--gray-color700);
    }
    &-btn {
      padding: 0 40px;
      font-weight: 700;
      font-size: 16px;
      line-height: 52px;
      color: var(--danger-color);
      background: var(--light-color);
      border-radius: 4px;
      border: 1px solid #ff4242;
      cursor: pointer;
      transition: 0.5s;
      &:hover {
        color: var(--light-color);
        background: var(--btn-gradient);
        transition: 0.3s;
      }
    }
  }
}
@include tablet {
  .events__card-info {
    flex-direction: column;
    gap: 20px;
  }
}
@include mobile {
  .events__card-time {
    flex-direction: column;
  }
  .events__card-text {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .events__card-btn {
    font-size: 13px;
    padding: 1px 22px;
  }
  .events__card {
    padding: 20px 20px;
  }
}
