.decor {
  &-1 {
    position: absolute;
    top: 133px;
    left: 103px;
  }
  &-2 {
    position: absolute;
    bottom: 0;
    left: 0;
  }
  &-3 {
    position: absolute;
    top: 0;
    right: 140px;
  }
  &-4 {
    position: absolute;
    right: 0;
    bottom: 194px;
  }
}
@include tablet {
  .decor {
    &-1 {
      left: 0;
      top: 326px;
    }
    &-2 {
      transform: translateY(100px);
    }
    &-3 {
      right: 49px;
    }
    &-4 {
      transform: translateX(100px);
    }
  }
}
